import React, { useState, useEffect } from 'react'
import BaseLayout from '../commonlayout/BaseLayout'
import BaseTitle from '../commonlayout/BaseTitle'
import { API_URL, BlockPopup, EXPLORER_TXN_URL_LIST, SITE_LINK, ToastPopup } from '../../actions/constant'

import axios from 'axios'
import AddInvoice from './AddInvoice'
import { Link } from 'react-router-dom'
import { copyTextToClipboard } from '../../actions/common'
import moment from "moment"


const InvoiceItem = ({ item }) => {

    var badge = "danger"
    if (item.status === "Paid") {
        badge = "success"
    }

    return <div className="row mb-2 mt-2">
        <div className="col">
            <div className="card mb-0">
                <div className="card-body">
                    <div className="row align-items-center justify-content-sm-between">
                        <div className="col-lg-1">
                            <div className="form-check">
                                <label className="form-check-label" htmlFor="task4">
                                    #{item.invoiceNo} {" "}
                                </label>
                            </div>{" "}
                            {/* end checkbox */}
                        </div>{" "}
                        <div className="col-lg-2">
                            <div className="form-check">
                                <label className="form-check-label" htmlFor="task4">
                                    {moment(item.createdAt).format("DD-MM-YYYY")}
                                </label>
                            </div>{" "}
                            {/* end checkbox */}
                        </div>{" "}
                        <div className="col-lg-2">
                            <div className="form-check">
                                <label className="form-check-label" htmlFor="task4">
                                    {item.invoiceAmount} {item.currencySymbol}
                                </label>
                            </div>{" "}
                            {/* end checkbox */}
                        </div>{" "}
                        {/* end col */}
                        <div className="col-lg-3">
                            {!item.txnHash ? <span className="badge badge-soft-secondary rounded-pill">
                                Paid Details Not Avaiable
                            </span> : <label className="form-check-label" htmlFor="task4">
                                {parseFloat(item.paidAmount).toFixed(4)} {item.assetSymbol} <br />
                                <small>{item.assetPrice}/{item.assetSymbol}</small>
                            </label>}
                        </div>
                        <div className="col-lg-1">
                            {item.txnHash ? <a href={EXPLORER_TXN_URL_LIST[item.chainId] + item.txnHash} target="_blank">
                                <span className="badge badge-soft-secondary rounded-pill">
                                    View Txn

                                </span>
                            </a> : <span className="badge badge-soft-secondary rounded-pill">
                                ~
                            </span>}
                        </div>
                        <div className="col-lg-3">
                            <ul className="list-inline text-sm-end mb-0">

                                <li className="list-inline-item pe-3" id="tooltip-container8">
                                    <span
                                        className="fs-13 fw-medium"

                                    >
                                        <span style={{ cursor: "pointer" }} onClick={() => {
                                            copyTextToClipboard(`${SITE_LINK}/viewinvoice/${item._id}`)
                                        }} className="icon icon-xxs text-normal">
                                            Share

                                        </span>

                                    </span>
                                </li>
                                <li className="list-inline-item pe-3" id="tooltip-container8">
                                    <span
                                        className="fs-13 fw-medium"

                                    >
                                        <a target="_blank" href={`/viewinvoice/${item._id}`}>
                                            <span className="icon icon-xxs text-normal">
                                                View

                                            </span>
                                        </a>

                                    </span>
                                </li>
                                <li className="list-inline-item" >
                                    <span className={`badge badge-soft-${badge} p-1`} style={{ width: 100 }}>{item.status}</span>
                                </li>
                            </ul>
                        </div>{" "}
                        {/* end col */}
                    </div>
                </div>
            </div>
        </div>
    </div>
}


const Invoice = () => {
    const [data, setdata] = useState([])
    const [hasNextPage, sethasNextPage] = useState(false)
    const [currentPage, setcurrentPage] = useState(1)
    const [moreloading, setmoreloading] = useState(false)




    useEffect(() => {
        getData()
    }, [])

    const getData = async () => {
        BlockPopup.standard('.invoicelist', `Please wait...`);
        try {
            let result = await axios.get(API_URL + `/api/invoice/?limit=10&page=${currentPage}`)
            BlockPopup.remove('.invoicelist');
            if (result.data) {
                setdata(result.data.docs)
                sethasNextPage(result.data.hasNextPage)
                setcurrentPage(result.data.page)
            }

        } catch (err) {
            BlockPopup.remove('.invoicelist');
            ToastPopup.failure("Error Ocuured in API")
        }
    }

    const onLoadMore = async () => {
        setmoreloading(true)
        BlockPopup.standard('.invoicelist', `Please wait...`);
        try {
            let result = await axios.get(API_URL + `/api/invoice/?limit=10&page=${currentPage + 1}`)
            BlockPopup.remove('.invoicelist');
            setmoreloading(false)

            if (result.data) {
                var tempres = data.concat(result.data.docs)
                setdata(tempres)
                sethasNextPage(result.data.page)
                setcurrentPage(result.data.hasNextPage)
            }

        } catch (err) {
            setmoreloading(false)
            BlockPopup.remove('.invoicelist');
            ToastPopup.failure("Error Ocuured in API")
        }
    }

    var invoiceTable;

    if (data.length > 0) {
        invoiceTable = data.map(result => {
            return <InvoiceItem item={result} />
        })
    } else {
        invoiceTable = (<div className='col-md-12'>
            <div className="card">
                <div className="card-body">
                    <div className="row">
                        <h1>No Record Available</h1>
                    </div>
                </div>
            </div>
        </div>)
    }
    return (
        <BaseLayout>
            <section class="position-relative p-3 bg-gradient2 min100vh">
                <div class="container">
                    <BaseTitle title={"Invoices"} subtitle={"All your invoices in one place"} />

                    <div className='row mt-2 m4-2'>
                        <div className='col-lg-12'>
                            <div class="row">
                                <div class="col">
                                    <h4 class="mb-3 mt-0 fs-16">Invoices</h4>
                                </div>
                                <div class="col text-end">
                                    <button type="button" class="btn btn-soft-primary col-3 me-2" data-bs-toggle="modal" data-bs-target="#addinvoiceModal">New Invoice</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='invoicelist'>

                        {invoiceTable}

                    </div>

                    {hasNextPage && !moreloading && <div className="row mb-3 mt-4">
                        <div className="col-12">
                            <div className="text-center">
                                <button onClick={() => onLoadMore()} className="btn btn-outline-primary btn-sm" type="button">
                                    {/* <span
                                        className="spinner-border spinner-border-sm me-1"
                                        role="status"
                                        aria-hidden="true"
                                    /> */}
                                    Load More
                                </button>
                            </div>
                        </div>{" "}
                        {/* end col*/}
                    </div>}


                </div>
            </section>
            <AddInvoice getInvoiceList={getData} />
        </BaseLayout>
    )
}

export default Invoice
