import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { AVAILABLE_ASSETS, BlockPopup, RPC_URL_BY_CHAIN, EXPLORER_TXN_URL_LIST, ToastPopup, addressRegex, API_URL, SITE_LINK } from '../../actions/constant'
import truncateEthAddress from 'truncate-eth-address'
import TextField from '../common/TextField'
import axios from 'axios'




const AddInvoice = ({ getInvoiceList }) => {

    const { chainId, signer, provider } = useSelector(state => state.web3)
    const { user } = useSelector(state => state.auth)

    const [loading, setloading] = useState("")

    //Sent Token Forms

    const [invoiceAmount, setinvoiceAmount] = useState("")
    const [description, setdescription] = useState("")
    const [errors, seterrors] = useState({})
    const [invoiceStatus, setinvoiceStatus] = useState({})




    useEffect(() => {
        setinvoiceStatus({})
    }, [invoiceAmount, description])




    const triggerAddToken = async (e) => {
        e.preventDefault()
        if (invoiceAmount === "") {
            ToastPopup.failure("Fill All the Fields")
            return
        }

        BlockPopup.standard('.addinvoice', `Please wait... Don't Close`);

        try {
            const insertData = {
                invoiceAmount,
                description,
                currencySymbol: "USD",
            }
            let result = await axios.post(API_URL + "/api/invoice", insertData)
            if (result.data) {
                setinvoiceAmount("")
                setdescription("")
                setinvoiceStatus(result.data)
                BlockPopup.remove('.addinvoice');
                ToastPopup.success("Invoice Successfully Added")
                getInvoiceList()
            }
        } catch (err) {
            BlockPopup.remove('.addinvoice');
            if (err && err.response && err.response.data) {
                seterrors(err.response.data)
            }
        }





    }











    return (
        <div>
            <>

                <div
                    id="addinvoiceModal"
                    className="modal fade "
                    tabIndex={-1}
                    role="dialog"
                    aria-labelledby="myModalLabel"
                    aria-hidden="true"
                >

                    <div className="modal-dialog">

                        <div className="modal-content addinvoice">
                            <div className="modal-body">

                                <div>
                                    <div>
                                        <div className="row g-0">
                                            <div className="col-md-12">
                                                <div className="p-xl-5 p-3">
                                                    <div className="text-center mb-5">

                                                        <img
                                                            src="assets/images/logo.png"
                                                            className="align-self-center"
                                                            alt="logo-img"
                                                            height={40}
                                                        />

                                                    </div>
                                                    <h3 className="h3 mb-0 mt-3 text-center">Create New Invoice</h3>
                                                    <p className="text-muted mt-1 mb-4 text-center">
                                                        Craft, Share, and Simplify Invoices with Ease!
                                                    </p>
                                                    {/*form start*/}
                                                    <form onSubmit={triggerAddToken} className="authentication-form">
                                                        <div className="mb-3">
                                                            <TextField
                                                                isRequired
                                                                type="number"
                                                                label="Enter Amount in USD"
                                                                onChange={(e) => setinvoiceAmount(e.target.value)}
                                                                value={invoiceAmount}
                                                                placeholder=""
                                                                error={errors.invoiceAmount}
                                                            />

                                                        </div>
                                                        <div className="mb-3">

                                                            <TextField

                                                                label="Purpose of Invoice"
                                                                onChange={(e) => setdescription(e.target.value)}
                                                                value={description}
                                                                placeholder=""
                                                                error={errors.description}
                                                            />

                                                        </div>

                                                        {invoiceStatus && Object.keys(invoiceStatus).length > 0 && <div role="alert" class="alert text-success bg-white border alert-dismissible">
                                                            <div class="d-flex align-items-start">
                                                                <span class="badge badge-soft-success align-self-center me-3">Success</span>
                                                                <div class="w-100">Your invoice  is created <a href={`${SITE_LINK}/viewinvoice/${invoiceStatus._id}`} target="_blank" class="text-success alert-link">View Invoice</a></div>
                                                            </div>
                                                            <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
                                                        </div>}


                                                        <div className="mb-0 text-center d-grid">
                                                            {!loading ? <button className="btn btn-primary" type="submit">
                                                                Create Invoice
                                                            </button> : <button class="btn btn-outline-primary btn-sm" type="button">
                                                                <span class="spinner-border spinner-border-sm me-1" role="status" aria-hidden="true"></span>
                                                                Please Wait..
                                                            </button>}
                                                        </div>
                                                    </form>

                                                </div>
                                            </div>

                                        </div>
                                    </div>{" "}
                                    {/* end card-body */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>

        </div>
    )
}

export default AddInvoice
