import React from 'react'

const BaseTitle = ({ title, subtitle }) => {
    return (

        <div class="row">
            <div class="col-lg-12">
                <div class="page-title">
                    <h3 class="my-0">{title}</h3>
                    <p class="mt-1 fw-medium">{subtitle}</p>
                </div>
            </div>
        </div>

    )
}

export default BaseTitle
